.candidate_form input{
    height:10px;
    margin-top: 2px;
}
.savebtn{
    width: 8%;
    height: 2em;
    background-color: rgb(32,177,255);
    transition:0.5s ;
    border-radius: 00.25em;
    cursor: pointer;

}
.savebtn:hover{
    transform: scale(1.25);
}
.buttons .navibtn{
    margin-top: 10px;
    margin-left: 5px;
    width: 8%;
    height: 2em;
    background-color: rgb(32,177,255);
    transition:0.5s ;
    border-radius: 00.25em;
    cursor: pointer;
   
}
