@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Pro:wght@200&family=Merriweather+Sans&family=Montserrat&family=Poppins:wght@300;400;500;600&family=Roboto:ital,wght@0,700;1,500&display=swap');
*{
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
.main{
    height: 100vh;
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.cover{
    background-color:white ;
    width: 30em;
    height: 25em;
    position: relative;
    border-radius: 1em;
    box-shadow: 0 0.188em 1.550em rgb(156,156,156);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

}
input{
    border: none;
    height: 3em;
    width: 70%;
    border-radius: 0.25em;
    text-align: center;
    padding: 2em;
    background-color: rgb(229,226,226);
}
input:focus{
    outline-color: rgb(32,177,255);
}
.btn{
    width: 20%;
    height: 2em;
    background-color: rgb(32,177,255);
    display: flex;
    justify-content: center;
    align-items: center;
    transition:0.5s ;
    border-radius: 00.25em;
    cursor: pointer;

}
.btn:hover{
    transform: scale(1.25);
}
